import React from 'react';
import { graphql, navigate } from 'gatsby';
import { GatsbySeo } from 'gatsby-plugin-next-seo';
import Img from 'gatsby-image';

import Layout from '../components/Layout';
import Container from '../components/Container';
import Banner from '../components/Banner';
import BlockHeaderNavSEMLP from '../components/BlockHeaderNavSEMLP';

import BlockHeaderNoImage from '../components/BlockHeaderNoImage';
import BlockHeaderWithImage from '../components/BlockHeaderWithImage';
import SpaceDivider from '../components/SpaceDivider';
import BlockLogos from '../components/BlockLogos';
import BlockCTA from '../components/BlockCTA';

import Block4Part from '../components/Block4Part';
import Block4PartCompanies from '../components/Block4PartCompanies';
import Footer from '../components/Footer';

import { edgesByName } from '../utils/gql';
import Button from '../components/Button';

export const pageQuery = graphql`
  {
    simpleTire: file(name: { eq: "simple-tire" }) {
      childImageSharp {
        fluid(maxWidth: 520) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    simpleTireSmall: file(name: { eq: "simple-tire-small" }) {
      childImageSharp {
        fluid(maxWidth: 244) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    companies: allContentfulCompany {
      edges {
        node {
          name
          image {
            fluid(maxWidth: 150) {
              ...GatsbyContentfulFluid
            }
          }
        }
      }
    }
    OndemandTiresImage: contentfulCompany(name: { eq: "Ondemand tires" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    LEDMyplaceImage: contentfulCompany(name: { eq: "LEDMyplace" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
    ikioImage: contentfulCompany(name: { eq: "ikio" }) {
      image {
        fixed(width: 130) {
          ...GatsbyContentfulFixed
        }
      }
    }
  }
`;

export default function SEMLandingPage({ data }) {
  const companies = edgesByName(data.companies, node => node.image.fluid);
  function handleContactUsClick() {
    navigate('/contact-sales');
  }
  return (
    <Layout>
      <GatsbySeo title="Resolve vs Apruve" />
      <Banner />
      <Container>
        <BlockHeaderNavSEMLP />
        <BlockHeaderNoImage
          title="B2B companies choose Resolve for all of their payment terms and invoicing needs"
          desc="Offer flexible net payment terms and automate your invoicing through Resolve"
          primaryButtonText="Get A Demo"
          primaryButtonTo="/contact-sales"
        />

        <SpaceDivider height={100} />

        <BlockLogos
          data={[
            {
              image: companies['CozyEarth'],
              name: 'CozyEarth',
              offsetTop: 0,
            },
            {
              image: companies['Hyperikon'],
              name: 'Hyperikon',
            },
            {
              image: companies['Interior Define'],
              name: 'Interior Define',
              offsetTop: 0,
            },
            {
              image: companies['SimpleTire'],
              name: 'SimpleTire',
              offsetTop: 0,
            },
            {
              image: companies['Real Thread'],
              name: 'Real Thread',
              offsetTop: -6,
            },
          ]}
        />

        <SpaceDivider height={100} />

        <Block4Part
          title="Take the guesswork out of offering net payment terms"
          items={[
            {
              title: 'Get Paid Faster',
              content: (
                <div>We deposit your funds within 2 days of the order</div>
              ),
              image: companies['CozyEarth'],
            },
            {
              title: 'Increase Your AOV',
              content: (
                <div>Encourage larger orders by offering net payment terms</div>
              ),
              image: companies['CozyEarth'],
            },
            {
              title: 'Reduce Risk',
              content: (
                <div>
                  We have guaranteed payments, so you don't have to worry about
                  receivables
                </div>
              ),
              image: companies['CozyEarth'],
            },
          ]}
        />

        <SpaceDivider height={100} />

        <Block4PartCompanies
          title="Innovative companies see results with Resolve"
          mainItem={{
            image: data.OndemandTiresImage.image.fixed,
            imageDesc: 'Ondemand tires company logo',
            review: (
              <span>
                "When a customer of ours uses Resolve,
                <br />
                they're significantly more likely to buy from
                <br />
                us again and again down the road."
              </span>
            ),
          }}
          items={[
            {
              id: 'led-my-place',
              image: data.LEDMyplaceImage.image.fixed,
              imageDesc: 'LEDMyplace company logo',
              review: '20% Increase in average order size',
            },
            {
              id: 'ikio',
              image: data.ikioImage.image.fixed,
              imageDesc: 'ikio company logo',
              review: 'Increase in corporate buyers',
            },
          ]}
        />
        <SpaceDivider height={100} />

        <BlockCTA
          pretitle="Ready to get started?"
          title="Join some of the world's most innovative companies who trust Resolve"
          primaryButtonText="Get A Demo"
          primaryButtonTo="/contact-sales"
          align="center"
        />
      </Container>
      <Footer />
      <style jsx global>{`
        .sem-lp__section {
          text-align: center;
        }

        .sem-lp__contact-us-button {
          display: block;
          margin: 0 auto;
        }

        .main-image {
          max-height: 300px;
        }
      `}</style>
    </Layout>
  );
}
